body{
    font-family: Montserrat;
    font-size: 16px;
    line-height: 1;
    height: 100%;
    color: #252B42;
}

*,
*::before,
*::after{
    padding: 0;
    margin: 0;
    border: 0;
    box-sizing: border-box;
}

a{
    text-decoration: none;
    color: inherit;
    margin: 0;
    padding: 0;
}

ul, ol, li{
    list-style: none;
    margin: 0;
    padding: 0;
}

h1,h2,h3,h4,h5,h6{
    font-style: inherit;
    font-weight: inherit;
}

img{
    vertical-align: top;
}

.wrapper{
    min-height: 100%;
    overflow: hidden;
}

._container{
    max-width: 1045px;
    margin: 0 auto;
}

._ibg{
    position: relative;
    img{
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        object-fit: cover;
        z-index: 1;
    }
}

input, textarea{
    &:active, &:hover, &:focus {
        outline: 0;
        outline-offset: 0;
    }
}

textarea{
    resize: none;
}